<template>
  <section class="online-exam-guide">
    <div class="ol-content-box">
      <section class="user">
        <aside class="user-img"><img v-if="user.avatar" :src="user.avatar | imgUrlFormat"></aside>
        <main class="user-info">
          <p>
            <small></small>
            <strong>{{user && user.nickname}}</strong></p>
          <p>
            <small></small>
            <strong>{{user && user.phone}}</strong></p>
        </main>
        <count-down class="count-down" :timer="time">
          <strong>
            
          </strong>
        </count-down>
      </section>
    </div>
    <div class="exam-remark ol-content-box ">
      <main class="exam-remark-main ">
        <h3>*</h3>
        <p>30</p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p>31</p>
        <footer style="text-align: center">
          <el-button type="danger" :disabled="!isEnd" :loading="!isEnd" @click="onStartExam()">
            
          </el-button>
        </footer>
      </main>
    </div>
  </section>
</template>
<script>
import CountDown from './CountDown'
import Timer from '@/commons/count-down-timer'

export default {
  data () {
    return {
      allTimerId: null,
      time: {
        hour: 'x',
        min: 'xx',
        second: 'xx'
      },
      isEnd: false,
      user: this.$store.state.user
    }
  },
  created: function () {
    let allTimer = {
      // todo  
      offsetSecond: 3,
      callback: this.onAllTimeChanged,
      endCallback: this.onAllTimerEnd
    }
    this.allTimerId = Timer.add(allTimer)
    Timer.startCountDown()
  },
  destroyed: function () {
    Timer.remove(this.allTimerId)
  },
  components: {
    'count-down': CountDown
  },
  methods: {
    onAllTimerEnd: function (timing) {
      this.isEnd = true
    },
    onAllTimeChanged: function (timing) {
      this.time = timing
    },
    onStartExam: function () {
      this.$router.replace({path: `/exam-center/${this.$route.params.id}/exam-pre`})
    }
  }
}
</script>

<style scoped lang="scss">
  .online-exam-guide {
    .user {
      display: flex;
      justify-content: center;
      .user-img {
        display: inline-block;
        width: 9em;
        height: 9em;
        border-radius: 50%;
        overflow: hidden;
        border: 1px dashed #ddd;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .user-info {
        margin-left: 2em;
      }
      .count-down {
        margin-left: 111px;
        text-align: center;
      }
    }
    .exam-remark {
      text-align: center;
      .exam-remark-main {
        text-align: left;
        display: inline-block;
      }
    }
  }
</style>



