export class Code {
  static Low = 1
  static Medium = 2
  static High = 3
  static SuperHigh = 4

  static UserCommit = 1
  static AdminCommit = 2
  static Finished = 3

  static Finance = 1
  static Account = 2
  static Paper = 3
  static Suggestion = 4
  static Other = 5

  static Recharge = 1
  static Profit = 2
  static Purchase = 3
  static Withdraw = 4
  static Admin = 5
  static Frozen = 6

  static Man = 1
  static Woman = 2
  static Unknow = 0

  static Success = 'success'

  static ticketStatus (code) {
    switch (code) {
      case Code.UserCommit:
        return ''
      case Code.AdminCommit:
        return ''
      case Code.Finished:
        return ''
      default:
        return ''
    }
  }

  static ticketFlag (code) {
    switch (code) {
      case Code.Low:
        return ''
      case Code.Medium:
        return ''
      case Code.High:
        return ''
      case Code.SuperHigh:
        return ''
      default:
        return ''
    }
  }

  static ticketModule (code) {
    switch (code) {
      case Code.Finance:
        return ''
      case Code.Account:
        return ''
      case Code.Paper:
        return ''
      case Code.Suggestion:
        return ''
      case Code.Other:
        return ''
      default:
        return ''
    }
  }

  static rechargeStatus (code) {
    switch (code) {
      case Code.Success:
        return ''
      default:
        return ''
    }
  }

  static gender (code) {
    switch (code) {
      case Code.Man:
        return ''
      case Code.Woman:
        return ''
      default:
        return ''
    }
  }

  static walletLogStatus (code) {
    switch (code) {
      case Code.Recharge:
        return ''
      case Code.Profit:
        return ''
      case Code.Purchase:
        return ''
      case Code.Withdraw:
        return ''
      case Code.Admin:
        return ''
      case Code.Frozen:
        return ''
      default:
        return ''
    }
  }
}



