<template>
  <el-dialog class="modify-password" title="" :visible.sync="dialogFormVisible" width="455px">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="99px">
      <el-form-item label="" prop="phone">
        <el-input type="text" v-model="ruleForm.phone" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone_code">
        <el-input placeholder="" v-model="ruleForm.phone_code" class="input-with-select">
          <el-button slot="append" @click="getPhoneCode('ruleForm')">{{time?`${time}`:''}}</el-button>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="ruleForm.password" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="pass_repeat">
        <el-input type="password" v-model="ruleForm.pass_repeat" auto-complete="off"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="onHidden()"> </el-button>
      <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import {AccountService, SecurityService} from '@/service/api'

export default {
  name: 'ModifyPassword',
  props: ['visible'],
  computed: {
    dialogFormVisible: {
      get: function () {
        return this.visible
      },
      set: function (val) {
        this.$emit('update:visible', val)
      }
    }
  },
  data () {
    let validatePassRepeat = (rule, value, callback) => {
      if (value !== this.ruleForm.password) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    let validatePhone = (rule, value, callback) => {
      let reg = 11 && /^((13|14|15|17|18)[0-9]{9})$/
      if (!reg.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      time: 0,
      ruleForm: {
        password: '',
        pass_repeat: '',
        phone: '',
        phone_code: ''
      },
      rules: {
        password: [
          {required: true, message: '', trigger: 'change'},
          {min: 6, max: 32, message: ' 6  32 ', trigger: 'change'}
        ],
        pass_repeat: [
          {required: true, message: '', trigger: 'change'},
          {validator: validatePassRepeat, trigger: 'change'}
        ],
        phone: [
          {required: true, message: '', trigger: 'change'},
          {validator: validatePhone, trigger: 'change'}
        ],
        phone_code: [
          {required: true, message: '', trigger: 'change'},
          {min: 6, max: 6, message: ' 6 ', trigger: 'change'}
        ]
      }
    }
  },
  methods: {
    getPhoneCode: function () {
      if (this.time) {
        return
      }
      this.$refs['ruleForm'].validateField('phone', (valid) => {
        if (valid) {
          return false
        }
        SecurityService.sms(this.ruleForm.phone)
          .then(res => {
            if (res) {
              this.time = 60
              this.countDown()
              this.$message.success('')
            } else {
              this.$message.error('')
            }
          })
      })
    },
    countDown: function () {
      let self = this
      setTimeout(function () {
        self.time -= 1
        if (self.time) {
          self.countDown()
        }
      }, 999)
    },
    onHidden: function () {
      this.dialogFormVisible = false
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        AccountService.resetPass(this.ruleForm)
          .then(res => {
            if (res) {
              // token,
              this.$message.success('')
              this.$router.push({path: '/account-home/login'})
            }
          })
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .modify-password {
  }
</style>



