<template>
  <section class="account-reg">
    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="111px" class="reg-form">
      <el-form-item label="" prop="phone">
        <el-input placeholder="" type="text" v-model="ruleForm.phone" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone_code">
        <el-input placeholder="" v-model="ruleForm.phone_code" class="input-with-select">
          <el-button slot="append" @click="getPhoneCode('ruleForm')">{{time?`${time}`:''}}</el-button>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="username">
        <el-input placeholder="" type="text" v-model="ruleForm.username" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input placeholder="" type="text" v-model="ruleForm.email" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input placeholder="" type="password" v-model="ruleForm.password" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="pass_repeat">
        <el-input placeholder="" type="password" v-model="ruleForm.pass_repeat" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="invite_code">
        <el-input placeholder="" type="text" v-model="ruleForm.invite_code" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')" style="width: 100%;"></el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
import {AccountService, SecurityService} from '@/service/api'

export default {
  data () {
    let validatePassRepeat = (rule, value, callback) => {
      if (value !== this.ruleForm.password) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    let validatePhone = (rule, value, callback) => {
      let reg = 11 && /^((13|14|15|17|18)[0-9]{9})$/
      if (!reg.test(value)) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      time: 0,
      ruleForm: {
        phone: '',
        phone_code: '',
        username: '',
        email: '',
        pass_repeat: '',
        password: '',
        invite_code: ''
      },
      rules: {
        phone: [
          {required: true, message: '', trigger: 'change'},
          {validator: validatePhone, trigger: 'change'}
        ],
        phone_code: [
          {required: true, message: '', trigger: 'change'},
          {min: 6, max: 6, message: ' 6 ', trigger: 'change'}
        ],
        username: [
          {required: true, message: '', trigger: 'change'},
          {min: 6, max: 32, message: ' 6  32 ', trigger: 'change'}
        ],
        email: [
          {required: true, message: '', trigger: 'change'},
          {min: 6, max: 64, message: ' 6  64 ', trigger: 'change'},
          {type: 'email', message: '', trigger: 'change'}
        ],
        password: [
          {required: true, message: '', trigger: 'change'},
          {min: 6, max: 32, message: ' 6  32 ', trigger: 'change'}
        ],
        pass_repeat: [
          {required: true, message: '', trigger: 'change'},
          {validator: validatePassRepeat, trigger: 'change'}
        ]
      }
    }
  },
  methods: {
    countDown: function () {
      let self = this
      setTimeout(function () {
        self.time -= 1
        if (self.time) {
          self.countDown()
        }
      }, 999)
    },
    getPhoneCode: function () {
      if (this.time) {
        return
      }
      this.$refs['ruleForm'].validateField('phone', (valid) => {
        if (valid) {
          return false
        }
        SecurityService.sms(this.ruleForm.phone)
          .then(res => {
            if (res) {
              this.time = 60
              this.countDown()
              this.$message.success('')
            } else {
              this.$message.error('')
            }
          })
      })
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          return false
        }
        AccountService.register(this.ruleForm).then(
          res => {
            if (res) {
              this.$message.success('')
              if (this.$router.currentRoute.query.redirect) {
                this.$router.push({path: this.$router.currentRoute.query.redirect})
              } else {
                this.$router.push({path: '/user-center/exam-list'})
              }
            }
          })
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .account-reg {
    .reg-form {
      max-width: 366px;
      margin: 0 auto;
    }
  }
</style>



