<template>
  <div class="online-exam-pre">
    <count-down class="count-down" :timer="time">
      <strong>
        
      </strong>
    </count-down>
    <div class="container">
      <section class="main" v-once>
        <div class="header ol-content-box">
          <section>
            <h2></h2>
            <article>
              <p>60102828</p>
              <p></p>
              <p></p>
            </article>
          </section>
        </div>
        <div class="ol-content-box">
          <section>
            <h3></h3>
            <article>
              <p></p>
              <p><img src="../../../../assets/q1.png">
              </p>
              <p>16623</p>
              <p>A B C D E</p>
              <p>A B C D E</p>
              <p></p>
              <p>16448~6721E</p>
              <p>24921~5636B</p>
            </article>
          </section>
        </div>
        <div class="ol-content-box">
          <section>
            <h3></h3>
            <article>
              <p>0</p>
              <p><img src="../../../../assets/q2.png"></p>
              <p>3    </p>
              <p>
                A0
                B1
                C2
                D3
                E4
              </p>
              <p>
                A0
                B1
                C2
                D3
                E4
              </p>
              <p></p>
              <p>32C</p>
              <p>4A</p>
            </article>
          </section>
        </div>
        <div class="ol-content-box">
          <section>
            <h3></h3>
            <article>
              <p></p>
              <p><img src="../../../../assets/q3.png"></p>
              <p>5</p>
              <p>
                ALQ74
                B76QP
                CL67P
                DL69P
                EL67Q
              </p>
              <p>6</p>
              <p>
                AQ607
                B940Q
                C9607
                D96Q7
                E049Q
              </p>
              <p></p>
              <p>5L67PC</p>
              <p>6940QA</p>
            </article>
          </section>
        </div>
      </section>
    </div>
  </div>
</template>
<script>
import CountDown from './CountDown'
import Timer from '@/commons/count-down-timer'
import {AccountService} from '@/service/api'

export default {
  data () {
    return {
      allTimerId: null,
      time: {
        hour: 'x',
        min: 'xx',
        second: 'xx'
      }
    }
  },
  created: function () {
    AccountService.refresh()
    let allTimer = {
      // todo  120s
      offsetSecond: 12,
      callback: this.onAllTimeChanged,
      endCallback: this.onAllTimerEnd
    }
    this.allTimerId = Timer.add(allTimer)
    Timer.startCountDown()
  },
  destroyed: function () {
    Timer.remove(this.allTimerId)
  },
  components: {
    'count-down': CountDown
  },
  methods: {
    onAllTimerEnd: function () {
      this.$router.replace({
        path: `/exam-center/${this.$route.params.id}/exam-room`
      })
    },
    onAllTimeChanged: function (timing) {
      this.time = timing
    }
  }
}
</script>

<style scoped lang="scss">
  .online-exam-pre {
    height: 100%;
    overflow: hidden;
    position: relative;
    .container{
      height: 100%;
      overflow-y: auto;
      .main {
        margin: 0 222px;
        .header {
          position: sticky;
          margin: 0 -22px;
          top: -20px;
        }
        .text-align-center {
          text-align: center;
        }
      }
    }
    .count-down {
      position: absolute;
    }
  }
</style>



